import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'mdb-react-ui-kit/dist/css/mdb.min.css';
import "@fortawesome/fontawesome-free/css/all.min.css";
import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';
import Sidebar from './components/Sidebar';
import Dashboard from './components/Dashboard';
import LineChart from './components/chart/LineChart';
import ProgressBar from './components/chart/ProgressBar';
import SignUp from './components/signup/SignUp';
import Login from './components/login/Login';
import ApprovedSeller from './components/seller/ApprovedSeller';
import RejectedSeller from './components/seller/RejectedSeller';
import ApprovedBuyer from './components/buyer/ApprovedBuyer';
import RejectedBuyer from './components/buyer/RejectedBuyer';
import SellerTransaction from './components/seller/SellerTransaction';
import BuyerTransaction from './components/buyer/BuyerTransaction';
import SellerSupport from './components/seller/SellerSupport';
import Complaint from './components/seller/Complaint';
import Feedback from './components/seller/Feedback';
import SellerInvoice from './components/seller/SellerInvoice'
import PendingInvoice from './components/seller/PendingInvoice'
import PaidInvoice from './components/seller/PaidInvoice'
import BuyerInvoice from './components/buyer/BuyerInvoice'
import BuyerPending from './components/buyer/BuyerPending';
import BuyerPaid from './components/buyer/BuyerPaid';
import BuyerSupport from './components/buyer/BuyerSupport'
import BuyerComplaint from './components/buyer/BuyerComplaint';
import BuyerFeedback from './components/buyer/BuyerFeedback';
import ApprovedProduct from './components/products/ApprovedProduct';
import RejectedProduct from './components/products/RejectedProduct';
import BuyerRequest from './components/BuyerRequest';
import SellerRequest from './components/SellerRequest';

function IncludeSidebar() {
  return (
    <div>
      <Sidebar>
        <Routes>
          <Route path="/" element={<Dashboard />} />
          <Route path="/line-chart" element={<LineChart />} />
          <Route path="/progress-bar" element={<ProgressBar />} />
          <Route path="/seller-request" element={<SellerRequest />} />
          <Route path="/buyer-request" element={<BuyerRequest/>} />          
          <Route path="/approved-seller" element={<ApprovedSeller />} />
          <Route path="/rejected-seller" element={<RejectedSeller />} />
          <Route path="/approved-buyer" element={<ApprovedBuyer />} />
          <Route path="/rejected-buyer" element={<RejectedBuyer />} />
          <Route path="/seller-transaction" element={<SellerTransaction />} />
          <Route path="/buyer-transaction" element={<BuyerTransaction />} />
          <Route path="/seller-support/complaint" element={<SellerSupport />} />
          <Route path="/seller-support/feedback" element={<SellerSupport />} />
          <Route path="/seller-support" element={<Navigate to="/seller-support/complaint" />} />
          <Route path="/complaint" element={<Complaint />} />
          <Route path="/feedback" element={<Feedback />} />
          <Route path="/seller-invoice/paid" element={<SellerInvoice/>} />
          <Route path="/seller-invoice/pending" element={<SellerInvoice/>} />
          <Route path="/seller-invoice" element={<Navigate to="/seller-invoice/paid" />} />
          <Route path="/pending-invoice" element={<PendingInvoice/>} />
          <Route path="/paid-invoice" element={<PaidInvoice/>} />
          <Route path="/approved-product" element={<ApprovedProduct/>} />
          <Route path="/rejected-product" element={<RejectedProduct/>} />
          {/* start the buyer route */}
          <Route path="/buyer-invoice/paid" element={<BuyerInvoice/>} />
          <Route path="/buyer-invoice/pending" element={<BuyerInvoice/>} />
          <Route path="/buyer-invoice" element={<Navigate to="/buyer-invoice/paid" />} />
          <Route path="/buyer-invoice" element={<BuyerInvoice/>} />
          <Route path="/buyer-pending" element={<BuyerPending/>} />
          <Route path="/buyer-paid" element={<BuyerPaid/>} />
          <Route path="/buyer-support/complaint" element={<BuyerSupport/>} />
          <Route path="/buyer-support/feedback" element={<BuyerSupport/>} />
          <Route path="/buyer-support" element={<Navigate to="/buyer-support/complaint" />} />
          <Route path="/buyer-complaint" element={<BuyerComplaint/>} />
          <Route path="/buyer-feedback" element={<BuyerFeedback/>} />    
          {/* end the buyer route */}
        </Routes>
      </Sidebar>
    </div>
  )
}

function App() {
  return (
    <>
      <div className='App'>
        <BrowserRouter>
          <Routes>
            <Route path="/*" element={<IncludeSidebar />} />
            <Route path="/sign-up" element={<SignUp />} />
            <Route path="/login" element={<Login />} />
          </Routes>
        </BrowserRouter>
      </div>
    </>
  );
}

export default App;
